* {
  margin: 0;
  padding: 0;
}

.wrapper {
  max-width: 1170px;
  margin: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

.header {
  background-color: #e2e2e2;
}

.header .wrapper {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-family: 'RobotoCondensed-Bold';
  color: #4e4e4e;
}

.header .wrapper .btnmenu,
.header .wrapper #menucheck {
  display: none;
}

.header .wrapper .title {
  font-size: 40px;
  margin: 45px 0;
}

.header .wrapper ul {
  display: flex;
  margin: 55px 0;
}

.header .wrapper ul li {
  margin: 0 10px;
  font-size: 16px;
}

.header .wrapper ul li a:hover {
  color: #1abb9c;
  transition: .3s;
}

.section_slider .item {
  position: relative;
}

.section_slider .item .description {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  margin-top: 150px;
  font-family: 'RobotoSlab-Bold';
}

.section_slider .item .description h1 {
  font-size: 40px;
  color: #f3f3f3;
}

.section_slider .item .description p {
  font-size: 32px;
  color: #a8a8a8;
  margin: 50px;
}

.section_slider .item .description a:hover {
  color: #fff;
}

.section_service .items {
  display: flex;
  justify-content: space-around;
  text-align: center;
  border-bottom: 1px solid #9e9e9e;
}

.section_service .items .item {
  margin: 60px 0;
}

.section_service .items .item h4 {
  margin: 34px 0 17px 0;
  font-family: 'RobotoSlab-Bold';
  font-size: 18px;
}

.section_service .items .item p {
  font-family: 'Roboto';
  font-size: 14px;
  color: #616161;
}

.section_works .title {
  font-family: 'RobotoCondensed-Bold';
  font-size: 18px;
  text-transform: uppercase;
  margin-top: 60px;
  margin-bottom: 25px;
}

.section_works .items {
  display: flex;
  justify-content: space-between;
}

.section_works .items .item h2 {
  font-family: 'RobotoSlab-Bold';
  color: #4e4e4e;
  font-size: 18px;
  margin-top: 25px;
  margin-bottom: 10px;
}

.section_works .items .item span {
  font-family: 'RobotoCondensed-Regular';
  font-size: 13px;
  color: #bebebe;
}

.section_works .items .item p {
  font-family: 'Roboto';
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 60px;
  color: #5c5c5c;
  line-height: 27px;
}

.section_quote .item {
  position: relative;
}

.section_quote .item .quotes {
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.section_quote .item .quotes h2 {
  font-family: 'Arial Rounded MT Bold';
  font-size: 100px;
  color: #dcdcdc;
  margin-top: 30px;
}

.section_quote .item .quotes p {
  font-family: 'RobotoSlab-Regular';
  font-size: 18px;
  color: #a1a1a1;
  margin-top: -30px;
}

.footer {
  background: url('../img/footerbg.png');
  padding: 45px 0;
}

.footer .items {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #3a3a3a;
}

.footer .items .item h2 {
  font-family: 'RobotoCondensed-Bold';
  font-size: 18px;
  color: #4b4b4b;
  text-transform: uppercase;
}

.footer .items .item ul,
.footer .items .item .desc {
  font-family: 'Roboto';
  font-size: 14px;
  color: #c2c2c2;
  line-height: 25px;
  margin: 25px 0;
}

.footer .items .item ul i,
.footer .items .item .desc i {
  color: #a89574;
  margin-right: 10px;
}

.footer .rights {
  margin-top: 30px;
  color: #c2c2c2;
  font-family: 'Roboto';
  font-size: 14px;
}

.btn {
  text-transform: uppercase;
  color: #fff;
  display: inline-block;
  border-radius: 4px;
  background: #3f8dbf;
  padding: 16px 24px;
  font-family: 'RobotoCondensed-Regular';
  font-size: 14px;
  box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.2);
  border: none;
}

@media (max-width: 1200px) {
  .section_slider .item .description {
    line-height: 1px;
  }

  .section_slider .item .description h1 {
    font-size: 32px;
  }

  .section_slider .item .description p {
    font-size: 20px;
  }

  .section_works .title {
    text-align: center;
  }

  .section_works .items .item:nth-child(2) {
    margin: 0 10px;
  }

  .section_works .items .item h2 {
    font-size: 17px;
  }

  .section_works .items .item span {
    font-size: 12px;
  }

  .section_works .items .item p {
    font-size: 13px;
  }

  .section_quote .item .quotes h2 {
    font-size: 80px;
  }

  .section_quote .item .quotes p {
    font-size: 15px;
  }

  .footer .items h2 {
    font-size: 17px;
  }

  .footer .items ul,
  .footer .items .desc {
    font-size: 13px;
  }

  .footer .rights {
    font-size: 13px;
    text-align: center;
  }
}

@media (max-width: 992px) {
  .section_slider .item .description {
    line-height: 1px;
    margin-top: 90px;
  }

  .section_slider .item .description h1 {
    font-size: 26px;
  }

  .section_slider .item .description p {
    font-size: 18px;
  }

  .section_service .items .item h4 {
    font-size: 16px;
  }

  .section_service .items .item p {
    font-size: 12px;
  }

  .section_works .items .item h2 {
    font-size: 16px;
  }

  .section_works .items .item span {
    font-size: 11px;
  }

  .section_works .items .item p {
    font-size: 12px;
  }

  .section_quote .item .quotes h2 {
    font-size: 60px;
    margin-top: 20px;
  }

  .section_quote .item .quotes p {
    font-size: 12px;
    margin-top: -30px;
  }

  .footer .items h2 {
    font-size: 15px;
  }

  .footer .items ul,
  .footer .items .desc {
    font-size: 12px;
    line-height: 22px;
    margin: 5px 0;
  }

  .footer .rights {
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .header .title {
    padding-left: 5px;
  }

  .header .menu ul {
    flex-direction: column;
    text-align: right;
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: -47px;
  }

  .section_slider .item .description {
    line-height: 1px;
    margin-top: 65px;
  }

  .section_slider .item .description h1 {
    font-size: 20px;
  }

  .section_slider .item .description p {
    font-size: 14px;
    margin: 30px;
  }

  .section_slider .item .description .btn {
    padding: 12px 18px;
    font-size: 12px;
  }

  .section_service .items {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .section_service .items .item {
    margin: 20px;
  }

  .section_service .items .item h4 {
    font-size: 16px;
  }

  .section_service .items .item p {
    font-size: 12px;
  }

  .section_service .items .item br {
    display: none;
  }

  .section_works .title {
    margin-top: 25px;
  }

  .section_works .items .item {
    text-align: center;
  }

  .section_works .items .item h2 {
    font-size: 14.5px;
    margin-top: 10px;
  }

  .section_works .items .item span {
    font-size: 13.5px;
  }

  .section_works .items .item p {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 12.5px;
  }

  .section_works .items .item:nth-child(1) {
    margin-left: 10px;
  }

  .section_works .items .item:nth-child(3) {
    margin-right: 10px;
  }

  .section_quote .item .quotes h2 {
    font-size: 60px;
    margin-top: 5px;
  }

  .section_quote .item .quotes p {
    font-size: 12px;
    margin-top: -35px;
  }

  .footer .items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: center;
  }

  .footer .items h2 {
    font-size: 16px;
  }

  .footer .items ul,
  .footer .items .desc {
    font-size: 12px;
  }

  .footer .rights {
    font-size: 12px;
    margin-top: 20px;
    margin-bottom: -25px;
  }
}

@media (max-width: 560px) {
  .section_slider .item .description {
    line-height: 1px;
    margin-top: 30px;
  }

  .section_slider .item .description h1 {
    font-size: 14px;
  }

  .section_slider .item .description p {
    font-size: 11px;
    margin: 20px;
  }

  .section_slider .item .description .btn {
    padding: 8px 14px;
    font-size: 8px;
  }

  .section_works .title {
    margin-top: 30px;
  }

  .section_works .items {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .section_works .items .item {
    margin: 0 10px;
    align-items: center;
  }

  .section_works .items .item h2 {
    margin-top: 5px;
    margin-bottom: 3px;
    font-size: 20px;
  }

  .section_works .items .item span {
    font-size: 16px;
  }

  .section_works .items .item p {
    font-size: 15px;
    margin-top: 3px;
  }

  .section_works .items .item br {
    display: none;
  }

  .section_quote .item img {
    min-height: 80px;
  }

  .section_quote .item .quotes h2 {
    font-size: 35px;
    margin-top: 5px;
  }

  .section_quote .item .quotes p {
    font-size: 10px;
    margin-top: -20px;
  }

  .section_quote .item .quotes br {
    display: none;
  }

  .footer .items {
    display: grid;
    grid-template-columns: 1fr;
    text-align: left;
    margin: 0 10px;
  }

  .footer .items .item:nth-child(1) .desc {
    margin-top: 4px;
    margin-bottom: -24px;
  }

  .footer .items .item:nth-child(2) ul {
    margin-top: 4px;
  }

  .footer .items .item:nth-child(3) .desc {
    margin: 4px;
  }

  .footer .items .item:nth-child(4) h2 {
    margin-top: 15px;
    margin-bottom: -20px;
  }

  .footer .items br {
    display: none;
  }

  .footer .items h2 {
    font-size: 16px;
  }

  .footer .items ul,
  .footer .items .desc {
    font-size: 12px;
  }

  .footer .rights {
    margin-top: 15px;
    margin-bottom: -30px;
    font-size: 12px;
  }
}